<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>&nbsp;&nbsp;<i class="el-icon-lx-lock"></i>&nbsp;&nbsp;&nbsp;资讯动态
        </el-breadcrumb-item>
        <el-breadcrumb-item>详情</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="form-box">
        <el-form ref="form" :model="form" label-width="160px">
          <el-form-item label="标题" prop="title" :label-width="formLabelWidth">
            <el-input v-model="form.title" disabled="" autocomplete="off" class="mz_wb"></el-input>
          </el-form-item>
          <el-form-item label="图片" class="padding" prop="img" :label-width="formLabelWidth">
            <el-upload
              class="avatar-uploader"
              :action="uploadUrl"
              name="img"
              disabled=""
              :show-file-list="false">
              <img v-if="imageUrl" :src="img+imageUrl" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="概况" prop="info" :label-width="formLabelWidth">
            <UE :defaultMsg=defaultMsg2 :config=config ref="ue2"></UE>
          </el-form-item>
          <el-form-item label="详情" prop="description" :label-width="formLabelWidth">
            <UE :defaultMsg=defaultMsg :config=config ref="ue"></UE>
          </el-form-item>
          <!--          </el-form-item>-->
          <el-form-item class="submit">
<!--            <el-button type="primary" @click="onSubmit('form')">提交</el-button>-->
            <el-button @click="$router.go(-1)">返回</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
  import http from '../../../assets/js/http'
  import UE from '../../common/uediter/ue.vue'

  export default {
    name: 'articleD',
    data: function () {
      return {
        img:window.HOST+'/',
        imageUrl: '',
        form: {},
        dialogVisible: false,
        dialogImageUrl: '',
        formLabelWidth: '100px',
        config: {
          initialFrameWidth: 831,
          initialFrameHeight: 350
        },
        defaultMsg: '',
        defaultMsg2: '',
      }
    },
    components: {
      UE
    },
    created() {

    },
    activated(){
      this.getV()
    },
    methods: {
      getV() {
        var that = this;
        var params = {
          'datoken': that.$route.query.datoken,
        };
        that.apiPost('/admin/djwzone', params).then((res) => {
          this.handelResponse(res, (data) => {
            that.form = data.data;
            that.imageUrl = that.form.minspic;
            that.$nextTick(() => {
              that.defaultMsg = data.data.info;
              that.defaultMsg2 = data.data.description;
              that.$refs.ue.setContent(that.form.info);
              that.$refs.ue2.setContent(that.form.description);
            })
          })
        })
      },
    },
    mixins: [http]
  }
</script>
<style>
  .el-upload--text {
    width: 180px !important;
  }
  .el-upload--text img {
    width: 100%;
    height: 100%;
  }
