  /*  border-color: #409EFF;*/
  /*}*/
  /*.avatar-uploader-icon {*/
  /*  font-size: 28px;*/
  /*  color: #8c939d;*/
  /*  width: 500px;*/
  /*  height: 178px;*/
  /*  line-height: 178px;*/
  /*  text-align: center;*/
